import http from '../../common/http.js'
export default {
	data() {
		return {
			imgUrl: http.imgUrl,
			address_id: null,
			type: '',
			is_default:false,
			regionModal:false,
			regionText:'请选择',
			regionObj:{}
		}
	},
	onLoad: function(e) {
		console.log(e)
		if (e.address_id == "0") {
			this.type = 'add'
		} else {
			this.type = 'edit'
			this.address_id = e.address_id
			uni.setNavigationBarTitle({
				title: '编辑地址'
			});
		}
		this.allRequest()
	},
	onShow: function() {

	},
	onPullDownRefresh: function() {

		// uni.stopPullDownRefresh(); //停止下拉刷新动画
	},
	onReachBottom: function() {

	},
	methods: {
		allRequest: function() {

		},
		aa: function() {
			uni.showLoading({
				title: '加载中'
			})
			let opts = {
				url: '',
				method: 'post'
			};
			let param = {
				city_id: '370700',
			};
			http.httpTokenRequest(opts, param).then(res => {
				console.log(res.data);
				uni.hideLoading()
			}, error => {
				console.log(error);
				uni.hideLoading()
			})
		},
		chooseRegion:function(e){
			
			console.log(e)
			this.regionText=e.province.label+'-'+e.city.label+'-'+e.area.label
			this.regionObj = e
		}
	}
}
